<template>

	<view class="content">
		<topbar bg_color="#F43B31" text_color="#fff" :have_top="true">
			<text class="icon-fanhui iconfont" style="color: #FFFFFF;" @click="fanhui"> </text>
			<text class=" " style="color: #FFFFFF;">圈子管理</text>
			<text class="pr5 fs14" style="color: #FFFFFF;"> </text>
		</topbar>
		<view class=" pl15 pr15 bgcf">
			<view class="flex ju_b aic pt8 pb8 b-b-1">
				<text class="fs15">修改头像</text>
				<avatar selWidth="250rpx" selHeight="250rpx" @upload="myUpload" :avatarSrc="avatar"
					avatarStyle="width: 52rpx;height: 52rpx;border-radius: 50%;" inner=true></avatar>
			</view>
			<view class="flex ju_b aic pt12 pb12 b-b-1">
				<text class="fs15">修改标题</text>
				<input type="text" placeholder="请输入手机号" v-model="opons" class="fs13 c9 text-right" />
			</view>
			<wangding-pickerAddress class=" b-b-1 " @change="change">
				<view class="flex aic ju_b  pb10 pt10">
					<view class="flex aic">
						<text class="fs15 ">修改地址</text>
					</view>
					<view class=" ">
						<text class="fs14  c9">{{city}}</text>
						<!-- <text class=" icon-arrow iconfont fs11 c9 pl5"></text> -->
						</image>
					</view>
				</view>
			</wangding-pickerAddress>
			<view class="pt12 pb12 b-b-1">
				<text class="fs15 ">修改简介</text>
				<textarea @input="descInput" v-model="desc" placeholder="请在这里输入圈子简介…" maxlength="500"
					placeholder-class="fs14  gaodu">
				</textarea>
				<view class="num  flex flex-right fs14"><text class="" style="color: #F43B31;">{{remnant}}</text>/500
				</view>
			</view>

			<view class="flex ju_b aic pt12 pb12 " @click='jump' data-url='../follow/follow'
				:data-shifou='true'>
				<text class="fs15">关注用户</text>
				<view class="flex aic">
					<text class="fs12 c9">6</text>
					<text class=" icon-arrow iconfont fs11 c9 pl5"></text>
				</view>
			</view>
		</view>
<view class="funbao flex aic ju_c fs15 ml15 mt25" @click="bao">
	保存
</view>
<view class="funbao flex aic ju_c fs15 ml15 mt15" @click="quxiao">
	删除
</view>
	</view>
</template>

<script>
	import api from '../../components/service.js';
	var that
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				avatar: '/static/images/ma/xue.jpg',
				opons: '企业服务',
				city: '北京',
				remnant: 0,
				desc: '',

			}
		},
		onHide() {

		},
		onLoad() {
			that = this
		},
		onShow() {

		},

		computed: {
			...mapState(['hasLogin', 'forcedLogin', 'userName', 'userinfo'])
		},
		methods: {
			...mapMutations(['logout', 'login']),
			getimg(img) {
				return api.getimg(img)
			},
			jump(e) {
				return api.jump(e)
			},
			descInput(e) {
				this.remnant = e.detail.value.length
			},
			fanhui() {
				wx.navigateBack({
					delta: 1
				})
			},
			quxiao() {
				uni.showModal({
					title: '提示',
					content: '确定删除圈子吗',
					success: function(res) {
						if (res.confirm) {
							uni.showToast({
								title: '删除圈子成功',
								icon: 'none',
							});
						} else if (res.cancel) {
							uni.showToast({
								title: '删除圈子失败',
								icon: 'none',
							});
						}
					}
				});
			},
			bao() {
				uni.showToast({
					title: '保存成功',
					icon: 'none',
				});
				setTimeout(function() {
				wx.navigateBack({
					delta: 1
				})
				}, 1000)
			},
			change(data) {

				this.city = data.data.join('')
				console.log(data.data.join(''))
				console.log(this.city);
			},
			myUpload(rsp) {
				that.avatar = rsp.path

				wx.uploadFile({
					url: api.IPurl + 'Base/upload', //仅为示例，非真实的接口地址
					filePath: that.avatar,
					name: 'file',
					formData: {

					},
					header: {
						'Authorization': uni.getStorageSync('token') || '',
					},
					success(res) {
						// const data = JSON.parse(res.data)
						// console.log(res.data)
						// that.avatar = data.data
						// that.getimg()
						//do something
					}
				})

			},

		}
	}
</script>

<style>
	page {
		background-color: #F8F8F8;
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}

	.content {
		background-color: #F8F8F8;
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}

	.gaodu {
		height: 100rpx;
	}

	uni-textarea {
		height: 150rpx !important;
		margin-top: 20rpx;
	}
	.funbao {
		width: 691rpx;
		color: #fff;
		height: 80rpx;
		background: #F43B31;
		border-radius: 10rpx;
	}
</style>
